const { Result } = require('@nbook/core');

const validTypes = [
    'line',
    'bar',
    'radar',
    'pie',
    'doughnut',
    'polarArea',
    'bubble',
    'scatter'
];

module.exports = {

    line(data = {}, options = {}) {
        this.plot('line', data, options);
    },

    bar(data = {}, options = {}) {
        this.plot('bar', data, options);
    },

    radar(data = {}, options = {}) {
        this.plot('radar', data, options);
    },

    pie(data = {}, options = {}) {
        this.plot('pie', data, options);
    },

    doughnut(data = {}, options = {}) {
        this.plot('doughnut', data, options);
    },

    polarArea(data = {}, options = {}) {
        this.plot('polarArea', data, options);
    },

    bubble(data = {}, options = {}) {
        this.plot('bubble', data, options);
    },

    scatter(data = {}, options = {}) {
        this.plot('scatter', data, options);
    },

    plot(type, data = {}, options = {}) {
        if(!type) {
            throw new Error('You must specify a plot type.');
        }

        if(!validTypes.includes(type)) {
            throw new Error(`You must specify a valid type (${validTypes.join(', ')}).`)
        }

        class PlotResult extends Result {
            getRenderer() {
                return {
                    path: this.relative(__dirname, './dist/chart.js')
                };
            }
    
            valueOf() {
                return { type, data, options };
            }
        }

        return new PlotResult;
    }

};
